	module.exports = {"data":{"markdownRemark":{"html":"<p>Python allows <code>str</code> objects, or <em>strings</em>, to be expressed in a few different ways:</p>\n<ul>\n<li>\n<p>Single quotes: <code>'Single quote strings can have \"double\" quotes inside.'</code></p>\n</li>\n<li>\n<p>Double quotes: <code>\"Double quote strings can have 'single' quotes inside.\"</code></p>\n</li>\n<li>\n<p>Triple quoted:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>\"\"\"Triple quoted strings can span multiple lines.\nUnescaped \"double\" and 'single' quotes in triple quoted strings are retained.\"\"\"\n\n'''Triple quoted strings can be 'single'or \"double\" quotes.\nUnescaped newlines are also retained.'''</code></pre>\n      </div>\n</li>\n<li>\n<p>Immutable: You cannot directly edit/change a Python string after you have created it. For example, if you try to directly reassign/change the first letter in a string, an error is thrown.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>>>> foo = \"my string\"\n>>> foo[0] = \"a\"\nTraceback (most recent call last):\n        File \"<stdin>\", line 1, in <module>\nTypeError: 'str' object does not support item assignment</code></pre>\n      </div>\n</li>\n</ul>\n<h2>Reference:</h2>\n<p><a href='https://docs.python.org/3/library/stdtypes.html#text-sequence-type-str' target='_blank' rel='nofollow'>Text Sequence Type <em>str</em></a></p>","fields":{"slug":"/python/data-structures/strings/"},"frontmatter":{"title":"The Python Strings"}}},"pathContext":{"slug":"/python/data-structures/strings/"}}

